  mounted() {},
  methods: {
    //进入页面调接口，获取卡片列表
    async refer() {
      let cardInfo = await txInfo(this.code)
      this.bankList = cardInfo.data.privateNolegalpersonAccount
      // console.log(this.bankList,'banklist')
      // this.payacc = this.bankList[0].accountName;
      // this.bindcard = this.bankList[0].accountNo;
      // this.bank = this.bankList[0].bankName;
      this.bankList.map(e => {
        var bankId = e.bankId.substr(1)
        if (!this.bankNameList[bankId]) {
          bankId = 'undefined'
        }
        e['bankName'] = this.bankNameList[bankId].bankName
        e['img'] = this.bankNameList[bankId].img
      })
      // console.log(this.bankList,'banklist')
    },
    choose(item, index) {
      this.payacc = item.accountName
      this.bindcard = item.accountNo
      this.bank = item.bankName
      var cardList = Array.from(document.getElementsByTagName('li'))
      // 选择哪张卡就把index赋值给它
      cardList.map((item, inx) => {
        if (inx == index) {
          this.isChoice = index
        }
      })
    },
    // 点击“保存”按钮
    onClickButton() {
      if (this.isChoice == 100) {
        Toast('请选择卡！')
      } else {
        this.$router.push({
          path: `/withdraw/${this.code}`,
          query: {
            payacc: this.payacc, //账户信息
            bindcard: this.bindcard, //绑定银行卡号
            bank: this.bank //银行名称
          }
        })
      }
    }
  }
}
