  box-sizing: border-box;
  background: url(../../assets/images/ssg/home_ssg.png) no-repeat top;
  background-size: 100%;
  &--title {
    color: @ft-white;
    font-size: @base-size * 2.7;
    padding: 0 @base-size*2.6 @base-size*2 @base-size*2.6;
    box-sizing: inherit;
  }
  li {
    list-style: none;
    width: 90%;
    height: 100%;
  }
  ul li:last-child {
    margin-bottom: @base-size*10;
  }
  li img {
    width: 100%;
    height: 100%;
    margin-top: -30%;
    margin-left: 5%;
  }
  .li_img {
    z-index: -1;
  }
  .li-card {
    transform: translateY(@base-size*1.5);
    margin-left: @base-size*3.5;
    .li-layout {
      .li-tag {
        background: @base-color;
        font-size: @base-size;
        float: right;
      }
      .li-content {
        height: @base-size * 3.5;
        padding-left: @base-size * 6;
        color: @ft-color-ssg;
        .li-name {
          font-size: @base-size * 1.6;
          line-height: 1em;
          font-weight: bold;
          padding-top: 20px;
        }
        .li-bankname {
          font-size: @base-size * 1.4;
          color: @ft-color-ssg;
          line-height: 1em;
          font-weight: bold;
          padding-top: @base-size / 4;
        }
      }
    }
    .li-num {
      display: block;
      font-size: @base-size * 2.4;
      color: @ft-color-ssg;
      margin: @base-size*2.5 0 @base-size*2.5 @base-size*1.5;
    }
  }
  .padding-bottom_fixed {
    padding-bottom: 25px;
  }

  .choosecard-btn {
    width: 80%;
    display: block;
    margin: @base-size*.5 auto @base-size*.5 auto;
    font-size: @base-size*1.8;
    color: @ft-color-ssg;
    background-color: @btn-color-ssg;
    border-radius: @base-size*5;
